*{
  margin:0;
  padding:0;
}

.after_root{
  width:100%;
  height:auto;
  background-color: rgb(224 222 222);
  display: flex;
  justify-content:center;
  flex-wrap: wrap;
  align-items: center;
}

.bigcard{ 
  display: inline-block;
  background-color: white;
  width:320px;
  box-shadow: 0.5px 0.5px 10px black;
  border-radius: 17px;
  margin: 70px 40px;
  height:400px;
}

.bigcard:hover{
  transform: scale(1.1,1.1);
}

.dtcard{
  padding:20px 20px;
}

.cardspan{
  text-transform: uppercase;
  font-family: sans-serif;
  color: rgb(181 145 145);
  font-size: 17px;
}

.cardhead{
  font-family:Verdana;
  text-transform: capitalize;
  font-weight: bold;
  margin:10px 0px;
}

img{
  width:320px;
  border-radius:17px 17px 0px 0px;
  height:250px;
}

.mainhead{
  width:100%;
  font-family: sans-serif;
  background-color: rgb(0, 0, 0);
  text-align: center;
  letter-spacing: 0.5px;
  word-spacing: 1px;
  color:rgb(255, 255, 255);
  padding:20px 0px;
}

.cardimg{
  object-fit: cover;
}

.button{
  cursor: pointer;
  padding:5px 5px;
  letter-spacing: 1px;
  word-spacing: 1px;
  width:150px;
  font-family: sans-serif;
  border: 2px solid black;
  margin-top:5px;
  text-transform: uppercase;
  border-radius: 5px;
}